<template>
  <div :title="$t('editUser')" id="editUser" style="display: inline-block;">
    <v-icon @click.stop="editUser(item)" v-text="icon" />
    <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-card>
            <v-card-title>
            <span class="headline">{{$t('editUser')}}</span>
            </v-card-title>

            <v-card-text>
                <v-container>
                  <v-form v-model="valid" :lazy-validation="lazy">
                    <v-row>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="editedItem.username" :rules="nameRules" disabled label="用户名"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="editedItem.password" label="密码" type="Password"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="editedItem.fullname" required :rules="nameRules" label="*姓名"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="editedItem.phone" label="联系方式" :rules="phoneRules"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="editedItem.email" label="邮箱" :rules="emailRules"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="editedItem.address" label="地址"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-select
                              v-model="editedItem.sex"
                              :items="['男', '女']"
                              label="性别"
                            ></v-select>
                        </v-col> 
                        <v-col cols="6" sm="6" md="6">
                            <Select-Roles
                              ref="selectRoles"
                              :oldRoleIds="editedItem.roleIds"
                            />
                        </v-col>
                        <v-col cols="12" sm="12" md="12">
                            <Select-DeptAnd-Positions
                                :departments="departments"
                                :selectDeptAndPositionNodes="selectDeptAndPositionNodes"
                                ref="SelectDeptAndPositions"
                            />
                        </v-col>
                    </v-row>
                  </v-form>
                </v-container>
            </v-card-text>

            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click.stop="close">取消</v-btn>
            <v-btn color="blue darken-1" text :disabled="!valid" @click.stop="save">保存</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
  </div>
</template>

<script>
import {required, email, phone} from '@/utils/validate'

export default {
  data: () => ({
    icon: "mdi-pencil",
    dialog: false,
    valid: true,
    lazy: false,
    desserts: [],
    editedItem: {
      id: "",
      username: "",
      password: "",
      fullname: "",
      phone: "",
      sex: "",
      email: "",
      address: "",
      uniqueCode: "",
      roleIds: []
    },
    
    selectDeptAndPositionNodes: [],

    //表单验证
    nameRules: [required],
    phoneRules: [phone],
    emailRules: [email],
  }),

  props: {
    departments: {
      type: Array
    },
    item: {
      type: Object
    }
  },

  computed: {
    
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
  },

  created() {
  },

  methods: {
    editUser(item) {
      this.editedItem = Object.assign({}, item);

      this.dialog = true;
      let _this = this;
      this.selectDeptAndPositionNodes = this.editedItem.selectDeptAndPositionNodes;
    },

    close() {
      this.$refs.selectRoles.roleIds = this.editedItem.roleIds;
      this.dialog = false;
    },

    save() {
      // 修改用户
      let params = {
        user: this.editedItem,

        roleIds: this.$refs.selectRoles.roleIds,
        selectDeptAndPositionNodes: this.selectDeptAndPositionNodes
      };

      this.$http
        .post("/user/api/v1/user/modify", params)
        .then(res => {
          if (res.success) {
            this.$emit("loadUsers");
            res.msg = "用户修改成功";
          }
          this.$store.commit("SET_SNACK", res);
        })
        .catch(error => {
          this.$store.commit("SET_SNACK", {
            success: false,
            msg: "访问异常：用户未修改"
          });
        });

        this.close();
    }
  },

  components: {
    SelectDeptAndPositions: () => import("./SelectDeptAndPositions"),
    SelectRoles: () => import("@/views/dashboard/component/roles/selectRoles"),
  }
};
</script>

